* {
  padding: 0px;
  margin: 0px;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

body {
  background-color: #233554;
}

#navbar {
  height: 100px;
  border: 1px solid red;
}

.intro {
  /* border: 1px solid red; */
  /* color: #e6f1ff; */
  width: max-content;
  margin-left: 100px;
  margin-top: 5rem;

}

.bright-text {
  color: #64ffda;
}

.intro>h1 {
  font-size: 60px;
  margin-top: 5px;
  margin-bottom: 5px;
}

.intro>div {
  margin-top: 30px;
  width: 40%;
}

.bright-white {
  color: #e6f1ff;
}

.s-dark-text {
  color: #a8b2d1;
}

.about {
  /* border: 1px solid red; */
  width: 70%;
  margin: auto;
  padding: 20px;
  margin-top: 200px;
  text-align: justify;
  line-height: 30px;
}

.about>div {
  /* border: 1px solid green; */
  display: flex;
  /* flex-wrap: wrap-reverse; */
  gap: 20;
  margin-top: 20px;
  justify-content: space-between;
}

.about>div>div:nth-child(1) {
  /* border: 1px solid yellow; */
  width: 70%;
}

.about>div>div:nth-child(2) {
  width: 250px;
  height: max-content;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #64ffda;
  /* box-shadow:  #64ffda 0px 1px 4px,  #64ffda 0px 0px 0px 3px; */
  /* box-shadow:#64ffda 0px 1px 0px,#64ffda 0px 8px 24px,#64ffda 0px 16px 48px; */
  box-shadow: #64ffda 0px 30px 90px;
}
#edu{
  border: 1px solid red;
  width: 40%;
  margin: auto;
  margin-top: 100px;
  margin-bottom: 100px;
}
#edu>div{
  width: 300px;
  height: 100px;
  border:1px solid #64ffda;
}

@media (max-width: 450px) {
  .intro {
    width: 90%;
    margin: auto;
    margin-top: 2rem;
  }

  .intro>h1 {
    font-size: 25px;
  }

  .intro>div {
    margin-top: 30px;
    width: 90%;
  }

  .about {
    width: 90%;
    margin: auto;
    margin-top: 100px;
    /* text-align:justify; */
    line-height: 20px;
  }

  .about>div {
    display: flex;
    flex-direction: column-reverse;
    gap: 20px;
  }

  .about>div>div:nth-child(1) {
    width: 100%;
  }

}

@media (min-width: 451px) and (max-width:850px) {
  .intro {
    width: 95%;
    margin: auto;
    margin-top: 2rem;
  }

  .intro>h1 {
    font-size: 40px;
  }

  .intro>div {
    margin-top: 30px;
    width: 60%;
  }

  .about {
    width: 90%;
    margin: auto;
    margin-top: 100px;
    line-height: 20px;
  }

  .about>div {
    display: flex;
    flex-direction: column-reverse;
    gap: 20px;
  }

  .about>div>div:nth-child(1) {
    /* border: 1px solid yellow; */
    width: 90%;
  }
}

